import styled from "styled-components"
import colors from "./Colors"

export const Container = styled.div`
    width: max-content;
    height: max-content;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Noto Sans JP', sans-serif;
    color: ${colors.blackGray};
    background-color: ${colors.lightGray};
    margin: 0;

    #profile {
            width: 17%;
            height: 100%;
            background-color: ${colors.blackGray};
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            position: fixed;
            margin-left: 15%;

            & > img {
                width: 42%;
                height: 15%;
                border-radius: 50%;
                margin-bottom: 50px;
            }

                & > a {
                    width: 20%;
                    height: 5%;
                    color: ${colors.lightGray};
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    transition: 200ms;
                    text-decoration: none;

                    :hover {
                        filter: opacity(75%);
                    }
                }
            }


    & > #about {
        height: 100vh;
        width: 100vw;
        max-width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        & > div {
            height: 100%;
            width: 70%;
            display: flex;

            & > #profile_content {
                width: 75%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-left: 35%;


                & > h1 {
                    font-size: 54px;
                    font-weight: 700;
                }

                & > p {
                    font-size: 20px;
                    font-weight: 400;
                }

                & > a {
                    color: ${colors.darkGray};
                    text-decoration: none;
                    cursor: pointer;
                    transition: 200ms;
                    :hover {
                        filter: opacity(75%);
                    }
                }

                & > div {
                    width: 12%;
                    display: flex;
                    justify-content: space-between;

                    & > a {
                        height: 50px;
                        width: 50px;
                        cursor: pointer;
                        transition: 200ms;
                        text-decoration: none;

                        :hover {
                            filter: opacity(75%);
                        }
                    }
                }
            }
        }
    }


    //===========================================================================================================================//

    & > #education {
        height: 100vh;
        width: 100vw;
        max-width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        & > div {
            height: 100%;
            width: 70%;
            display: flex;

            & > #education_content {
                width: 75%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-left: 35%;

                
                & > h1 {
                    font-size: 54px;
                    font-weight: 700;
                }

                & > ul {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                }
            }
        }
    }

    //===========================================================================================================================//

    & > #skills {
        height: 100vh;
        width: 100vw;
        max-width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        
        & > div {
            height: 100%;
            width: 70%;
            display: flex;

            & > #skills_content {
                width: 75%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-left: 35%;

                
                & > h1 {
                    font-size: 54px;
                    font-weight: 700;
                }
            }
        }
    }

    //===========================================================================================================================//

    & > #publications {
        height: 100vh;
        width: 100vw;
        max-width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        
        & > div {
            height: 100%;
            width: 70%;
            display: flex;

            & > #publications_content {
                width: 75%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-left: 35%;

                
                & > h1 {
                    font-size: 54px;
                    font-weight: 700;
                }
            }
        }
    }
`

export const Icons = {
    marginTop: "30px",
    height: "50px",
    width: "50px",
    color: `${colors.blackGray}`
}


export const SkillsContainer = styled.div`
    width: 100%;
    height: 70%;
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr 1fr 1fr 1fr;

    & > div {
        width: 60%;
        height: 50%;
        border-style: solid;
        border-radius: 8px;
        border-width: 1px;
        border-color: ${colors.darkGray};
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
`
